<template>
	<view class="content">

		<u-form :model="form02" ref="sjForm" label-position="top" :error-type="errorType">
			<u-form-item label="手机号" required prop="phone"><u-input v-model="form02.phone" maxlength="11" />
				<view @tap="getCode(0)" class="code">{{tips}}</view>
			</u-form-item>
			<u-form-item label="验证码" required prop="code"><u-input type="number" v-model="form02.code" /></u-form-item>

		</u-form>

		<view style="padding: 107rpx 0 30rpx 0;">
			<u-button type="primary" class="btn" @click="loginin">保存修改</u-button>
		</view>

		<u-toast ref="uToast"></u-toast>
		<u-verification-code :seconds="seconds" @end="end" @start="start" ref="uCode"
			@change="codeChange"></u-verification-code>

	</view>
</template>

<script>
	import {
		updatePhone,
		sendCodeByPhone
	} from '../../api/index.js'
	export default {
		data() {
			return {
				errorType: ['toast'],
				show: 1,
				tips: '',
				seconds: 60,
				form02: {
					phone: null,
					code: null
				},
				rulessj: {
					phone: [{
						required: true,
						message: '请输入手机号码',
						trigger: ['change', 'blur'],
					}, {
						validator: (rule, value, callback) => {
							return this.$u.test.mobile(value);
						},
						message: '手机号码不正确',
						trigger: ['change', 'blur'],
					}],
					code: [{
						required: true,
						message: '请填写验证码',
						trigger: ['change', 'blur']
					}]
				},
			}
		},
		onReady() {
			this.$refs.sjForm.setRules(this.rulessj);
		},
		methods: {
			loginin() {
				this.$refs.sjForm.validate(valid => {
					if (valid) {
						updatePhone(this.form02).then(res => {
							if (res.code == 200) {
								this.$u.toast('修改成功');
								uni.navigateTo({
									url: '../my/set?phone=' + this.form02.phone
								})
							} else {
								this.$u.toast(res.msg);
							}
						})
					}
				});
			},
			codeChange(text) {
				this.tips = text;
			},
			getCode(no) {
				if (this.form02.phone) {
					if (this.$refs.uCode.canGetCode) {
						// 模拟向后端请求验证码
						uni.showLoading({
							title: '正在获取验证码'
						})
						sendCodeByPhone({
							username: this.form02.phone
						}).then(res => {
							if (res.code == 200) {
								uni.hideLoading();
								// 这里此提示会被this.start()方法中的提示覆盖
								this.$u.toast('验证码已发送');
								// 通知验证码组件内部开始倒计时
								this.$refs.uCode.start();
							}
						})
					} else {
						this.$u.toast('倒计时结束后再发送');
					}
				} else {
					this.$u.toast('请输入手机号');
				}

			},
			end() {
				this.$u.toast('可重新发送验证码');
			},
			start() {
				//this.$u.toast('倒计时开始');
			},
		}
	}
</script>

<style scoped>
	.content {
		width: 100vw;
		height: 100vh;
		text-align: center;
		padding: 0 30rpx;
	}

	.content /deep/ uni-swiper {
		height: 410rpx;
	}

	.content>>>.uni-swiper {
		height: 300rpx;
	}

	.code {
		color: #296DCB;
		font-size: 28rpx;
		font-weight: bold;
	}

	.logo {
		height: 151rpx;
		width: 156rpx;
		margin-top: 102rpx;
		margin-bottom: 12rpx;
	}

	.title {
		font-size: 36rpx;
		font-weight: bold;
		color: #333333;
	}

	.smtitle {
		margin: 8rpx auto 40rpx auto;
		color: #777777;
	}

	.btn {
		width: 100%;
	}

	.minititle {
		font-size: 24rpx;
	}

	.minititle span {
		color: #296DCB;
	}

	.pt {
		width: 300rpx;
		height: 460rpx;
		background: url('@/static/img/zc.png') no-repeat center center;
		background-size: cover;
		box-sizing: border-box;
		text-align: center;
		color: #fff;
		font-size: 32rpx;
		padding-top: 360rpx;
		margin-top: 120rpx;
	}

	.blue {
		color: #296DCB;
	}

	.zyz {
		background: url('@/static/img/zyz.png') no-repeat center center;
		background-size: cover;
		box-sizing: border-box;
	}

	.tips {
		margin-top: 24rpx;
		margin-bottom: -20rpx;
	}

	.tips span {
		font-size: 24rpx;
		color: #333333;
		margin-left: 8rpx;
	}
</style>